body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  font-family: 'gilroy', Arial, sans-serif;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm-px; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  &.is-loaded:not(.no-fade-in) {
    animation: fadeIn 1s; } }

.out {
  &.has-fixed-header {
    padding-top: $header-height; } }

.container {
  padding-left: 50px;
  padding-right: 50px;
  @include xl {
    padding-left: 30px;
    padding-right: 30px; }
  @include lg {
    padding-left: 15px;
    padding-right: 15px; }
  &--sm {
    padding-left: 95px;
    padding-right: 95px;
    @include xl {
      padding-left: 30px + 45px;
      padding-right: 30px + 45px; } }
  &--xs {
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px !important;
    padding-right: 15px !important;
    max-width: 940px; } }

.logo {
  display: inline-block;
  font-size: 0;
  @include lg {
    width: 70px; } }

.close {
  @include icon-close(20, 1, currentColor);
  position: absolute;
  z-index: 1;
  top: 15px;
  right: 15px; }

.contact-man {
  text-align: center;
  &__text {
    margin-bottom: 15px;
    max-width: 620px;
    margin-left: auto;
    margin-right: auto; } }

.accent {
  color: $accent; }

.price {
  font-size: $fz-sm;
  font-weight: 700;
  color: $accent;
  small {
    font-weight: 300;
    color: $gray-100;
    font-size: $fz-sm-px; }
  &--lg {
    @include css-lock(24, 36, 320, 1399, font-size); } }

