.social {
  li {
    display: inline-block;
    background-color: currentColor;
    border-radius: 50%;
    @include tr(background-color .3s);
    @include hover {
      background-color: $accent; } }
  a {
    @include flex(center, center);
    color: $white;
    width: 25px;
    height: 25px;
    .icon {
      font-size: .8em; } } }
