.btn {
  @include inline-flex(center, center);
  color: $white;
  background-color: $accent;
  border: 1px solid  $accent;
  text-align: center;
  text-transform: uppercase;
  min-width: 180px;
  padding: .7em 1em .5em;
  @include tr(background-color .3s, color .3s, border-color .3s);
  @include hover {
    background-color: $white;
    color: $accent; }
  @include xs {
    min-width: 0;
    width: 100%; }
  .icon {
    font-size: 1.3em;
    margin-left: .4em; }

  &--transp-gray {
    color: #474747;
    background-color: transparent;
    border-color: $gray-light;
    @include hover {
      background-color: transparent;
      color: $accent;
      border-color: $accent; } }
  &--no-hover {
    @include hover {
      color: $white;
      background-color: $accent; } } }
