.footer {
  color: $gray-light;
  &__inner {
    @include flex(space-between, center);
    padding-top: 25px;
    padding-bottom: 25px;
    @include sm {
      display: block;
      text-align: center; } }
  &__left {
    @include flex(flex-start, center);
    @include md {
      justify-content: center;
      margin-bottom: 15px; } }
  &__right {
    text-align: right;
    @include md {
      text-align: center; } }
  &__copy {
    text-transform: uppercase;
    margin-right: 15px; } }
