.hero {
  color: $white;
  @include cover;
  &__inner {
    padding-top: 14%;
    padding-bottom: 11%;
    text-align: center; }
  &__title {
    margin-bottom: 10px;
    @include md {
      margin-bottom: 5px; } } }

.hero-slide {
  position: relative;
  @include cover;
  @include before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 30%;
    z-index: 0;
    background-image: linear-gradient(to top, rgba(0,0,0,0) 0%, rgb(0,0,0) 100%); }
  &__inner {
    position: relative;
    z-index: 1;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    @include flex(center, normal, column);
    @include xl {
      padding-left: 20px;
      padding-right: 20px; }
    @include lg {
      // padding-left: 25px
 } }      // padding-right: 25px
  &__content {
    max-width: 495px; }
  &__title {
 }    // margin-bottom: 10px
  &__subttl {
    margin-bottom: 15px; } }
