.section {
  padding-top: 65px;
  padding-bottom: 65px;
  @include xl {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include lg {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include md {
    padding-top: 30px;
    padding-bottom: 30px; }
  @include sm {
    padding-top: 20px;
    padding-bottom: 20px; }
  &__title {
    text-align: center;
    margin-bottom: 65px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }

  &--gray-lightest {
    background-color: $gray-lightest; }
  &--sm {
    padding-top: 25px !important;
    padding-bottom: 25px !important; } }
