.text-block {
  $this: &;
  @include flex(flex-start, normal, row, wrap);
  @include notlast {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__left, &__right {
    width: 50%;
    @include flex(center, normal, column);
    @include sm {
      width: 100%; } }
  &__left {
    @include sm {
      margin-bottom: 15px; } }
  &__content {
    padding-left: 45px;
    padding-right: 45px;
    text-align: justify;
    @include lg {
      padding-left: 15px;
      padding-right: 15px; }
    @include sm {
      padding-left: 0;
      padding-right: 0; }
    p {
      @include notlast {
        margin-bottom: 30px;
        @include md {
          margin-bottom: 15px; } } } }
  &__img {
    @include cover;
    padding-top: 50%; }

  &--reverse {
    #{$this}__left {
      order: 1;
      @include sm {
        order: 0; } }
    #{$this}__right {
      order: 0;
      @include sm {
        order: 1; } } } }
